import React from "react";
import ListItem from "./ListItem";

const ListTodo = ({ items, handleClick, editItem, editionMode }) => {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "column-reverse",
				alignItems: "center",
			}}
		>
			{items.map(value => (
				<ListItem
					value={value}
					key={value.id}
					deleteItem={handleClick}
					editItem={editItem}
					editionMode={editionMode}
				/>
			))}
		</div>
	);
};

export default ListTodo;
