import styled from "styled-components";
/* 
eeebdd
810000
630000
1b1717
*/

export const Item = styled.div`
	width: 90%;
	color: #eeebdd;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 10px;
	margin: 5px 0;
	border-left: 7px solid
		${props => (props.selectedItem ? "#810000" : "#eeebdd")};
	background-color: #630000;
	flex-wrap: wrap;
	overflow-x: auto;
`;

export const ItemText = styled.p`
	font-size: 1.2rem;
`;

export const Button = styled.button`
	background-color: ${props => (props.delete ? "#810000" : "#1b1717")};
	color: white;
	border: none;
	cursor: pointer;
	padding: 5px;
	margin-left: ${props => (props.delete ? "10px" : "0")};
	border-radius: 5px;
	width: 55px;
	font-weight: 600;
	&:focus {
		outline: none;
	}
`;
