import { Item, Button, ItemText } from "./style";

const ListItem = ({ value, deleteItem, editItem, editionMode }) => {
	return (
		<Item selectedItem={editionMode ? true : false}>
			<ItemText>{value.item}</ItemText>
			<div>
				<Button
					onClick={() => {
						editItem(value);
					}}
					disabled={editionMode ? true : false}
				>
					Edit
				</Button>
				<Button
					delete
					onClick={() => {
						if (window.confirm("You sure want to delete the note?"))
							deleteItem(value.id);
						else return;
					}}
					disabled={editionMode ? true : false}
				>
					Delete
				</Button>
			</div>
		</Item>
	);
};

export default ListItem;
