import styled from "styled-components";

export const Form = styled.form`
	display: flex;
	justify-content: center;
	height: 100px;
	align-items: center;
`;

export const InputInsert = styled.input`
	height: 47px;
	font-size: 1.4rem;
	padding: 0 10px;
	width: 40%;
	border: 1px solid #eeebdd;
	border-radius: 5px;
	color: #eeebdd;
	background-color: #1b1717;
	font-family: "Ubuntu Mono", monospace;

	&:focus {
		outline: none;
		border: 2px solid #eeebdd;
	}
`;
/* 
eeebdd
810000
630000
1b1717
*/

export const InputSubmit = styled.input`
	height: 50%;
	width: 60px;
	margin-left: 5px;
	border-radius: 5px;
	padding: 0 10px;
	font-size: 1.1rem;
	border: 1px solid #eeebdd;
	background-color: ${props => (props.editBoton ? "#1b1717" : "#eeebdd")};
	color: ${props => (props.editBoton ? "#eeebdd" : "#1b1717")};
	font-family: "Ubuntu Mono", monospace;
	cursor: pointer;
	&:focus {
		outline: none;
	}
	&:hover {
		background-color: ${props => (props.editBoton ? "#eeebdd" : "#1b1717")};
		color: ${props => (props.editBoton ? "#1b1717" : "#eeebdd")};
	}
`;
