import React, { useState, useEffect } from "react";
import { Form, InputInsert, InputSubmit } from "./style";

const InputTodo = ({ handleClick, editionMode, itemEdit, addEditsItem }) => {
	const [state, setState] = useState("");

	const cleanInput = () => {
		setState("");
	};

	useEffect(() => {
		editionMode && setState(itemEdit.item);
	}, [editionMode, itemEdit.item]);

	return (
		<Form
			onSubmit={e => {
				e.preventDefault();
			}}
		>
			<InputInsert
				placeholder="Enter a task..."
				type="text"
				onChange={e => {
					setState(e.target.value);
				}}
				value={state}
			/>
			<InputSubmit
				editBoton={editionMode ? true : false}
				type="submit"
				value={editionMode ? "Edit" : "Add"}
				onClick={() => {
					editionMode ? addEditsItem(itemEdit, state) : handleClick(state);
					cleanInput();
				}}
				disabled={!state.length}
			/>
		</Form>
	);
};

export default InputTodo;
