import React, { useState } from "react";
import InputTodo from "./InputTodo/";
import ListTodo from "./ListTodo";
import uniqid from "uniqid";

const TodoApp = () => {
	const [items, setItems] = useState([]);
	const [editionMode, setEditionMode] = useState(false);
	const [itemEdit, setItemEdit] = useState({});

	const addItem = item => {
		const itemObject = {
			item: item,
			id: uniqid(),
		};
		setItems([...items, itemObject]);
	};
	const deleteItem = idItem => {
		const itemFilter = items.filter(({ id }) => id !== idItem);
		setItems(itemFilter);
	};
	const editItem = valueItem => {
		setEditionMode(true);
		setItemEdit(valueItem);
	};
	const addEditsItem = (itemEdit, newItem) => {
		const editsItems = items.map(item =>
			item.id === itemEdit.id ? { id: item.id, item: newItem } : item
		);
		setItems(editsItems);
		setEditionMode(false);
		setItemEdit({});
	};

	return (
		<div
			style={{
				width: "85%",
				height: "90vh",
				backgroundColor: "#1b1717",
				overflowY: "auto",
			}}
		>
			<h1
				style={{
					textAlign: "center",
					color: "#eeebdd",
					margin: 0,
					marginTop: "10px",
					fontSize: "3rem",
				}}
			>
				Todo app
			</h1>
			<InputTodo
				handleClick={addItem}
				editionMode={editionMode}
				itemEdit={itemEdit}
				addEditsItem={addEditsItem}
			/>

			<ListTodo
				items={items}
				handleClick={deleteItem}
				editItem={editItem}
				editionMode={editionMode}
			/>
		</div>
	);
};

export default TodoApp;
